@import url(https://fonts.googleapis.com/css?family=Inconsolata);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  display: grid;
  grid-template-columns: 7fr 3fr;
  grid-template-rows: 2fr 4fr;
}
.editor{
  grid-row: 1/3;
}
.block{
  border: 1px solid #666;
  padding: 0.5rem;
}
.textBox{
  margin: 5px;
  display: block;
  height: 80%;
  width: 95%;
  resize: none;
  overflow: auto;
}
.output{
  font-family: 'Inconsolata', monospace;
  font-size: 1rem;
  margin: 10px;
  padding: 2px;
  height: 24rem;
  width: 96%;
  color: white;
  background-color: black;
  border: 1px solid rgb(169, 169, 169);
  overflow-y: auto;
  white-space: pre-line;
}
.upperTab_tab__2Ft3S{
    box-sizing: border-box;
    display: flex;
    height: 2.5rem;
    justify-content: space-between;
    align-items: center;
    width: 70vw;
    background-color: white;
    padding: 0 2px;
    color: #666;
}
.editorSetting_controlLabel__qOrd2{
    color: #555;
}
select{
    background-color: white;
}
.editorSetting_picker__LgHmD{
    width: 10rem;
    font-size: 1rem;
    outline: none;
    padding: 2px;
    margin: 0 1rem;
}
.editorSetting_picker__LgHmD:active{
    box-shadow: 0 0 10px #91c8f5;
}
.collaborators_collaborators__ZveAR{
    cursor: pointer;
}
.button_default__3KiSZ, .button_danger__f8tBy, .button_success__2lLij{
    padding: 0.6rem 1rem;
    background-color: #2f3640;
    color: white;
    font-size: 1rem;
    border: none;
    border-radius: 0.3rem; 
    cursor: pointer;
    outline: none;
}
.button_danger__f8tBy{
    background-color: #e74c3c;
}
.button_success__2lLij{
    background-color: #4cd137;
}
.button_default__3KiSZ:active, .button_danger__f8tBy:active, .button_success__2lLij:active{
    box-shadow: none;
}
